import Typography from "typography";

const typography = new Typography({
  baseFontSize: '18 px',

  headerFontFamily: ["HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", 'Helvetica', 'Arial', 'sans-serif'],
  headerWeight: 'normal',

  bodyFontFamily: ["HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", 'Helvetica', 'Arial', 'sans-serif'],
  bodyGray: 30
})


// if (process.env.NODE_ENV !== 'production') {
//   typography.injectStyles()
// }

export default typography




